
import React, {Component} from 'react';

class Content extends Component {
    constructor(props){
        super(props);
        this.state = {
            first: false,
            second: false,
            third: false,
        };
        this.handleToggleImage = this.handleToggleImage.bind(this);
    }

    handleToggleImage(number){
        switch (number){
            case 1:
                console.log(number);
                this.setState({first: !this.state.first});
                break;
            case 2:
                console.log(number);
                this.setState({second: !this.state.second});
                break;
            case 3:
                console.log(number);
                this.setState({third: !this.state.third});
                break;
            default:
                break;
        }
    }

    render(){
        const pathStatic = process.env.PUBLIC_URL;
        const { first, second, third } = this.state;
        console.log(first);
        console.log(second);
        console.log(third);

        return (
            <div className={'main'}>
                <div className={'name'}>
                    <h2>이재운 (Lee JaeWoon)</h2>
                </div>
                <div className={'description'}>
                    <h3>소개</h3>
                    <ul>
                        <li>웹 기술을 이용한 클라이언트 어플리케이션을 만들 수 있습니다</li>
                        <li>기술을 학습하고 공유하는 것을 지향합니다</li>
                    </ul>
                </div>
                <div className={'resume'}>
                    <h3>이력</h3>
                    <ul>
                        <li>키클롭스 웹 프론트엔드 개발 (2017/12 ~ 현재)</li>
                        <li>국민대학교 전자공학부 졸업, 소프트웨어학과 부전공 (2011/03 ~ 2018/02)</li>
                    </ul>
                </div>
                <div className={'curriculum-vitae'}>
                    <h3>경력 기술서</h3>
                    <p>Cyclops (키클롭스)</p>
                    <p>- 소개: K-POP 스트리밍 및 커뮤니티 서비스 Sming 프로젝트</p>
                    <p>- 담당 업무</p>
                    <ol>
                        <li>Sming 관리자 페이지 개발</li>
                        <ul>
                            <li>2018.07 - 현재</li>
                            <li>Sming 서비스의 관리를 원활하게 하는 페이지를 개발합니다</li>
                            <li>레거시 코드의 수정과 기존 기능 개선 및 새 기능을 요구 사항에 맞게 추가하는 것이 주 업무입니다</li>
                            <li>통계, 방송 모니터링, 유저, 이벤트와 공지, 앱 내 아이템, K-POP 굿즈, 커뮤니티, 재무 등의 관리 기능</li>
                        </ul>
                        <li>Sming 서비스용 웹 페이지 개발</li>
                        <ul>
                            <li>2018.05 - 현재</li>
                            <li>Sming 서비스의 <a href={'https://hello.sming.com'}>메인 웹 페이지</a> 및 <a href={'https://download.sming.com'}>방송 콘솔 다운로드 페이지</a></li>
                            <li>다양한 유저들의 유입과 사용을 원활하게 하기 위한 반응형 웹 페이지를 개발합니다</li>
                        </ul>
                        <li>Sming 이벤트 페이지 개발</li>
                        <ul>
                            <li>2018.02 - 현재</li>
                            <li>Sming 앱의 메인 탭에서 보여지는 모든 이벤트 페이지를 개발합니다</li>
                            <li>기기와 브라우저 호환성을 고려하여 반응형 웹 페이지로 제작합니다</li>
                            <li>이벤트 목적과 컨셉에 따라 다른 UI와 기능을 적용합니다</li>
                            <br/>
                            <li><h4>최근 이벤트들</h4></li>
                            <li>
                                <dt>Artist Week Series</dt>
                                <dd>
                                    <ul>
                                        <li><a href={'https://sming.page.link/xZRp'}>B.A.P</a></li>
                                        <li><a href={'https://sming.page.link/83ux'}>Momoland</a></li>
                                        <li><a href={'https://s3.ap-northeast-2.amazonaws.com/resource.sming.com/production/static/event/202/index.html?access=sneak'}>Golden Child</a></li>
                                    </ul>
                                </dd>
                            </li>
                            <li><a href={'https://s3.ap-northeast-2.amazonaws.com/resource.sming.com/production/static/event/235/index.html'}>Artist Week Vote 1st Week Winner</a></li>
                            <li>
                                <dt className={'detail'}
                                    onClick={()=>this.handleToggleImage(1)}>Aritst Week Vote</dt>
                                <dd className={first ? 'show' : 'hide'}
                                    onClick={()=>this.handleToggleImage(1)}>
                                    <ul>
                                        <li>
                                            <img src={pathStatic + '/images/1-1.jpg'}
                                                 alt={''}/>
                                            <img src={pathStatic + '/images/1-2.jpg'}
                                                 alt={''}/>
                                        </li>
                                        <li>
                                            <img src={pathStatic + '/images/1-3.jpg'}
                                                 alt={''}/>
                                            <img src={pathStatic + '/images/1-4.jpg'}
                                                 alt={''}/>
                                        </li>
                                        <li>
                                            <img src={pathStatic + '/images/1-5.jpg'}
                                                 alt={''}/>
                                            <img src={pathStatic + '/images/1-6.jpg'}
                                                 alt={''}/>
                                        </li>
                                    </ul>
                                </dd>
                            </li>
                            <li>
                                <dt className={'detail'}
                                    onClick={() => this.handleToggleImage(2)}>Get the Prize</dt>
                                <dd className={second ? 'show' : 'hide'}
                                    onClick={() => this.handleToggleImage(2)}>
                                    <ul>
                                        <li>
                                            <img src={pathStatic + '/images/2-1.jpg'}
                                                 alt={''}/>
                                            <img src={pathStatic + '/images/2-2.jpg'}
                                                 alt={''}/>
                                        </li>
                                    </ul>
                                </dd>
                            </li>
                        </ul>
                        <li>Sming 앱 내 웹뷰 및 팝업 페이지 개발</li>
                        <ul>
                            <li>2018.02 - 현재</li>
                            <li className={'detail'}
                                onClick={() => this.handleToggleImage(3)}>일일 로그인 팝업이나 방송 및 앱 내 다양한 기능들에서 보여주는 상세 설명 팝업을 요구 사항에 맞게 개발합니다</li>
                            <li className={third ? 'show' : 'hide'}
                                onClick={() => this.handleToggleImage(3)}>
                                <img src={pathStatic + '/images/3-1.jpg'}
                                     alt={''}/>
                            </li>
                        </ul>
                    </ol>
                    <p>- 사용 기술: HTML5, CSS3, NodeJS, ReactJS</p>

                    <h3>전문 기술</h3>
                    <p>전문 분야와 개발할 때 사용하는 도구입니다</p>
                    <ul>
                        <li>웹 표준, 크로스브라우징, 반응형 웹</li>
                        <li>HTML5</li>
                        <li>CSS3</li>
                        <li>JavaScript ES6, NodeJS, ReactJS</li>
                        <li>npm</li>
                        <li>Git, Bitbucket, SourceTree, GitHub</li>
                        <li>Chrome DevTools</li>
                        <li>WebStorm</li>
                        <li>Firebase Web</li>
                    </ul>

                    <h3>협업</h3>
                    <p>프로젝트 목표를 이해하고 기획 및 디자인 등 여러 직군과 소통합니다</p>
                    <ul>
                        <li>Slack, Email</li>
                        <li>프로젝트 및 이슈 관리: Bitbucket, JIRA, Confluence, Google Drive, Trello, GitHub</li>
                        <li>디자인: Zeplin</li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Content;