
import React, {Component} from 'react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCookie, faCookieBite, } from '@fortawesome/free-solid-svg-icons';

library.add(faCookie);
library.add(faCookieBite);
// library.add(faLanguage);

class Header extends Component {
    constructor(props){
        super(props);
        this.state = {
            logo: 'cookie',
        };
        this.handleChangeLogo = this.handleChangeLogo.bind(this);
    }

    handleChangeLogo() {
        let logo = this.state.logo === 'cookie' ? 'cookie-bite' : 'cookie';
        this.setState({logo});
    }

    render(){
        const { logo } = this.state;
        let color = logo === 'cookie-bite' ? '#D2691E' : '#800080';
        let spin = logo === 'cookie';

        return (
            <header className="App-header">
                <div className={'top'}>
                    <div className={'logo'}
                         onClick={this.handleChangeLogo}>
                        <FontAwesomeIcon icon={logo}
                                         size={'6x'}
                                         spin={spin}
                                         style={{color: color}}/>
                    </div>
                    <ul>
                        <li style={{color: color}}>
                            <p>About</p>
                        </li>
                        {/*<li>*/}
                        {/*<p>Others</p>*/}
                        {/*</li>*/}
                    </ul>
                </div>
                <div className={'language'}>
                    {/*<FontAwesomeIcon icon="language"/>*/}
                </div>
            </header>
        );
    }
}

export default Header;